html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #616161;
}

#root{
  height: 100%;
}
.App{
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  background: #616161;
  color: white;
}

.sidebar-view-container{
  display: flex;
  flex-flow: row nowrap;
  height: 100%;
}

.sidebar{
  min-width: 200px;
}

section {
  margin: 20px;
  width: 100%;
  height: calc(100% - 40px)
}

.section-actions{
  margin-bottom: 25px;
}

.footer-actions{
  flex-shrink: 0;
}

tbody > tr:hover{
  background-color: #515151;
  /* transition: .2s all ease-in-out; */
}

.buttonsInDialog {
  display: flex;
  flex-flow: row nowrap;
  padding: 0 24px 15px;
}

.dialog-action-delete{
  display: flex;
  flex-basis: 50%;
}

.dialog-action-others{
  display: flex;
  flex-basis: 50%;
  justify-content: flex-end;
}
.header-wrapper{
  min-height: 64px;
  background: #424242;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  padding: 0 10px;
}

.header-title{
  margin-left: 10px;
  flex-grow: 1;
}

.header-icon > span > svg{
  fill: white
}

.header-status-indicator{
  background: orange;
  width: 10px;
  height: 10px;
  margin-right: 10px;
  border-radius: 50%;
  -webkit-transform: 2s all ease-in-out;
          transform: 2s all ease-in-out;
}

.header-status-indicator-green{
  background: green;
  width: 10px;
  height: 10px;
  margin-right: 10px;
  border-radius: 50%;
  -webkit-transform: 2s all ease-in-out;
          transform: 2s all ease-in-out;
}

.header-status-indicator-red{
  background: red;
  width: 10px;
  height: 10px;
  margin-right: 10px;
  border-radius: 50%;
  -webkit-transform: 2s all ease-in-out;
          transform: 2s all ease-in-out;
}
.sidebar-container{
  min-width: 225px;
  border-right: 1px solid #545454;
  margin: 20px 0;
}

.list-link{
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

.list-item-icon > svg{
  fill: white !important;
}

.list-item-text > span{
  color: white;
}

.active{
  background: black !important;
}
.dashboardSection{
  display: block;
}

#aantalGebruikers{
  margin-bottom: 20px;
}

#meestVerkocht{
  margin-bottom: 20px;
}

#meestBekekenSchilder{
  margin-bottom: 20px;
}

#meestBekekenSchilderij{
  margin-bottom: 20px;
}

#minstBekeken{
  margin-bottom: 20px;
}

#minstVerkocht{
  margin-bottom: 20px;
}

#aantalGebruikers{
  padding-bottom:20px;
}
.login-container{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #616161;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.login-wrapper{
  display: flex;
  flex-flow: column nowrap;
  padding: 50px;
}

.login-logo{
  fill: white;
}

.login-wrapper > h1{
  margin-bottom: 20px;
  text-align: center;
}

.login-wrapper > h2{
  margin-bottom: 0;
}

.login-form{
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: 25px;
}
.add-user-review-container{
  display: flex;
  flex-flow: column nowrap;
  color: white;
  margin: 0 !important;
  font-size: 1.1em;
}

.add-user-review-container-divider{
  background: grey;
  height: 1px;
  padding: 0 !important;
  margin: 0 20px !important;
}

.add-user-name-surname{
  text-align: center;
  font-size: 1.7em !important;
}

.add-user-role{
  text-align: center;
  margin-top: 5px;
}

.add-user-column-left{
  text-align: right;
}

.add-user-column-right{

}
.dialog-add-painting{
  min-width: 500px;
}

.add-painting-review-img-preview{
  max-width: 100%;
  max-height: 100%;
  color: white;
}

.add-painting-review-img-preview-container{
  display: flex;
  flex-flow: column nowrap;
  align-self: center;
}

.add-painting-review-details-preview{
  display: flex;
  flex-flow: column nowrap;
  align-self: center;
}

.add-painting-review-container{
  display: flex;
  flex-flow: column nowrap;
  color: white;
  margin: 10px 0 !important;
}

.add-painting-review-container > span:first-child{
  font-size: .8em;
}

.add-painter-review-img-preview{
  width: 100%;
  height: 200px;
  object-fit: cover;
}


